           width: 70px;\n            background: $addBtn;\n            color: #fff;\n            float: right;\n          }\n        }\n        td {\n          border: 1px solid $essentialColour;\n          padding: 8px 10px;\n        }\n        .tabActive {\n          background: $essentialColour;\n        }\n      }\n      .Vote-Table-List {\n        margin-bottom: 15px;\n        position: relative;\n        >.el-button{\n            position: absolute;\n            top:0;\n            right:-45px;\n            z-index: 200;\n            border-color:$essentialColour;\n            border-top-left-radius: 0;\n            border-bottom-left-radius: 0;\n        }\n        .vote-table {\n          border-collapse: collapse;\n          position: relative;\n          width: 100%;\n          th {\n            border: 1px solid $essentialColour;\n          }\n          td {\n            border: 1px solid $essentialColour;\n            padding: 8px 10px;\n            &:hover{\n                >.fa-times{\n                  display: block;\n                }\n              }\n            .options {\n              width: 95%;\n              border: 0;\n              outline: none;\n            }\n            >.fa-times{\n                float:right;\n                display: none;\n                padding:4px;\n              }\n          }\n          .footer-table-td11,\n          .footer-table-td12,\n          .footer-table-td13,\n          .footer-table-td16 {\n            width: 20%;\n            text-align: center;\n            color: $essentialColour;\n            background: $firstList;\n          }\n          >tr>td:nth-of-type(2) {\n            padding-left: 10px;\n          }\n          >tr:last-of-type>td:nth-of-type(2) {\n            text-align: center;\n          }\n          .footer-table-td23,\n          .footer-table-td24,\n          .footer-table-td25 {\n            padding-left: 10px;\n            .el-checkbox {\n              font-weight: normal;\n         